body, html {
  height: 100%;
  width: 100%;
  padding: 0;
  margin: 0;
}

.flex-spacer {
  flex-grow: 1;
}

#root {
  height: 100%;
  width: 100%;
}
.app {
  display: flex;
  flex-direction: row;
  height: 100%;
  width: 100%;
  background-color: lightgray;
}
.app2 {
  display: flex;
  flex-direction: column;
}

.panel {
  padding: 8px 25px;
  border-radius: 1rem !important;
}

.head {
  border-style: none none solid none;
  border-color: rgba(0, 0, 0, 0.28);
  border-width: 1px;
  text-align: center;
}