.randomizer-root {
    text-align: center;
}

.randomizer-wrapper {
    padding: 36px 0;
    display: flex;
    flex-direction: row;
}
.randomizer-root .MuiButtonBase-root {
    margin: 0 10px;
}

.restaurant-card {
    padding: 8px;
    min-width: 320px;
    margin: 0 20px;
}
.randomizer-filters {
    text-align: center;
    margin: 10px 0 0 0;
}
.randomizer-filters > * {
    min-width: 80px !important;
    margin: 0 10px !important;
}
.randomizer-filters .MuiSelect-root {
    padding-left: 4px !important;
}